$accent1: #268bd2;
$accent2: #dc322f;
$fg: #657b83;
$bg: #fdf6e3;
$bg-alt: #eee8d5;

body {
  background-color: $bg;
  color: $fg;
}
ul.posts {
  list-style: none;
  padding: 0;
  margin: 0;
  li.posts__item {
    background-color: $bg-alt;
    margin-bottom: 1rem;
    padding: 1.25rem;
    .posts__title {
      margin: 0;
    }
    .posts__desc {
      margin-top: 1rem;
    }
  }
}
